html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  position: relative;
  text-decoration: none; }
  a::after {
    position: absolute;
    content: "";
    left: 50%;
    bottom: -3px;
    width: 0;
    height: 1px;
    transition: 0.3s;
    background: black; }
  a:hover {
    color: black; }
    a:hover::after {
      left: 0;
      width: 100%; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-size: 24px;
  font-family: "Candara", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@keyframes stroke-offset {
  0% {
    fill-opacity: 0;
    stroke-width: 0;
    stroke-dasharray: 0% 100%; }
  25% {
    fill-opacity: 0;
    stroke-width: 1px; }
  50% {
    fill-opacity: 0; }
  75% {
    stroke-width: 1px; }
  100% {
    stroke-width: 0; } }

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px; }
  .content--main {
    min-height: 100vh; }

.content__about-name {
  margin-bottom: 0; }
  .content__about-name svg text {
    fill: black;
    stroke: black;
    fill-opacity: 1;
    stroke-width: 0;
    stroke-dasharray: 100% 0;
    animation: stroke-offset 3s ease-out; }

.content__about-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }

.content__about-item {
  margin-right: 20px; }

.content__project {
  width: 100%; }

.content__project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none; }

.content__project-item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
  padding: 20px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  transition: all 0.3s; }
  .content__project-item:hover {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.07); }
  .content__project-item h3 {
    margin: 0; }
  .content__project-item p {
    font-size: 22px;
    color: #333333; }
  .content__project-item a {
    font-size: 20px; }
    .content__project-item a::after {
      left: 50%; }
    .content__project-item a:hover::after {
      left: 0;
      width: 100%; }

.content__project-link-wrapper {
  margin-top: auto; }

.header__logo {
  padding: 30px; }
